.subscribe-form {
    display: flex;
    justify-content: space-around;
    align-items: center;
    background: #faf2e4;
    padding: 20px;
    margin: 30px 0;

    h3 {
        text-align: left;
        font-size: 18px;
        line-height: 1.444;
        font-family: $prate;
        margin: 0;
    }

    &__title {
        width: 50%;
        flex-shrink: 0;
        flex-grow: 0;
    }

    &__form {
        width: 50%;
        flex-shrink: 0;
        flex-grow: 0;
        display: flex;
        justify-content: flex-start;

        input[type=text] {
            border: 0;
            background: #fff;
            height: 60px;
            padding: 20px;
            flex-grow: 1;
            font-size: 14px;
        }

        button {
            font-family: $semibold;
            font-size: 17px;
            width: 60px;
            height: 60px;
            border: 0;
            background: #d29f47;
            color: #fff;
        }
    }

    @media screen and (max-width: 767px){
        margin: 25px -20px 0 -20px;
        padding: 30px 20px;
        display: block;
        text-align: center;

        h3 {
            text-align: center;
            font-size: 18px;
            line-height: 1.444;
            margin: 0 0 15px 0;
        }

        &__title {
            width: auto;
            padding: 0 25px;
        }

        &__form {
            width: auto;
            display: block;

            input[type=text] {
                width: 100%;
                margin: 0 0 20px 0;
            }

            button {
                width: 100%;
            }
        }
    }
}